//***********
// Navbar
//***********

.navbar {
    background-color: #3c5132;
    height: 100px;
    z-index: 999;
    -webkit-box-shadow: 0px 2px 5px #7fc348;
    -moz-box-shadow: 0px 2px 5px #7fc348;
    box-shadow: 0px 2px 5px #7fc348;
    transition: top 1s;

    .navbar_link {
        padding: 10px;
        text-decoration: none;
        color: #7fc348;
        font-size: 20px;

        &:hover {
            color: #cbeeb1;
        }

        &-icon {
            text-decoration: none;
            color: #3c5132;
            display: block;

            .navbar_icon {
                padding: 10px;
                font-size: 30px;
                height: 30px;
            }

        }
    }
}
  
.carousel_container {
    margin-top: 5px;

    .carousel_item_container {
        height: 35vw;

        .image {
        width: 100vw;
        height: 40vw;
        }

    }
}
  
.sticky {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    width: 100%;
    transition: top 1s;
}

//***********
// Footer
//***********

.footer {
    background-color: #3c5132;
    margin-top: 100px;

    .footer_links_container {
       
        ul {
            list-style-type: none;
            margin: 0;
            padding: 0;
            
            li {
                padding: 2px 0;

                a {
                    font-size: 20px;
                }
            } 
        }
    }

    .footer_link {
        padding: 10px;
        text-decoration: none;
        color: #7fc348;
        font-size: 16px;

        &:hover {
            color: #cbeeb1;
        }
    }

    

    .footer_box {
        background-color: #3c5132;
        height: 30px;
        padding: 5px;
    }

    .footer_small_box {
        background-color: #1e261e;
        height: 30px;
        padding: 5px;
        width: 100%;
        margin-top: 20px;
    }

    .author {
        color: #cbeeb1;
        font-size: 12px;
        position: absolute;
        right: 10px;
        padding: 5px
    }
    .img_container {
        padding: 20px;
        
        img {
            width: 100%;
            height: 160px;
        }
    }

    .social-media_container {

        ul {
            display: flex;
            flex-direction: row;
            
            li {
                background-color: #7fc348;
                color: #3c5132;
                border-radius: 10px;
                margin-right: 20px;
                padding: 10px;

                &:hover {
                    opacity: 0.7;
                    color:white;

                    a {
                        opacity: 0.7;
                        color:white;
                    }
                }

                a {
                    color: #3c5132;
                    text-decoration: none;

                    &:hover {
                        opacity: 0.7;
                        color:white;
                    }
                }
                
            }
        }
    }
   
}
  
//***********
// Language Selector
//***********
  
.language-selector {
    position: relative;
    display: inline-block; 
    padding-left: 20px;

    &:hover {

        .language-selector-content {
            display: block;
        }

        .language-selector_btn {
            background-color: #3e8e41;
        }

    }

    .language-selector_btn {
        color: white;
        padding: 0;
        font-size: 16px;
        border: none;
        cursor: pointer;
        z-index: 999;
        line-height: 8px;
    }

    &-content {
        display: none;
        position: absolute;
        right: 0;
        background-color: #cbeeb1;
        min-width: 120px;
        box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
        z-index: 2;

        div {
            color: black;
            padding: 10px;
            display: block;
            cursor: pointer;
        

        &:hover {
            background-color: #7fc348;
            cursor: pointer;
        }

        .text {
            padding: 5px;
        }
        }
    }

    .language_icon {
        width: 25px;
    }
}

//***********
// Banner
//***********

.banner-container {
    margin-top: 5px;
    position: relative;

    img {
        width: 100%;
    }

    .banner_title_container {
        position: absolute;
        top: 100px;
        text-align: center;
        background-color: white;
        opacity: 0.7;
        width: 100%;

        .banner_title {
           margin: 0 100px
        }

    }
    

    .icon_arrow-down_container {
        position: absolute;
        bottom: 50px;
        left: 48%;
        animation-name: example;
        animation-duration: 1s;
        animation-iteration-count: infinite;

        @keyframes example {
            0%   {bottom: 50px;}
            50%  {bottom: 45px;}
            100% {bottom: 50px;}
          }

        .icon_arrow-down {
            font-size: 50px;
            color: #3c5132;
            background-color: white;
            border-radius: 50px;
        }

        a:hover {

            .icon_arrow-down {
                font-size: 50px;
                color: #7fc348;
            }
        }
    }

    .banner_btn_container {
        position: absolute;
        bottom: 150px;
        display: flex;
        width: 100%;
        justify-content: center;
       
    
        .banner_btn_contact {
            background-color: #3c5132;
            border: 2px solid #7fc348;
            border-radius: 20px;
            color: #7fc348;
            font-size: 20px;
            padding: 20px;
            text-decoration: none;
            
    
            &:hover {
                color: #cbeeb1;
                background-color: #3c5132;
                -webkit-box-shadow: 0px 2px 5px 2px #7fc348;
                -moz-box-shadow: 0px 2px 5px 2px #7fc348;
                box-shadow: 0px 2px 5px 2px #7fc348;
    
            }
        }
    }

}

.page_title {
    text-align: center;
    padding: 100px;
}

//***********
// Main Container
//***********

.main_container {
    
    .img_container {
        padding: 10px;

        img {
            width: 100%;
            display: block;
        }
    }

    .text_container {
        padding: 10px;
        font-size: 20px;
        color: white;
    }
}

.btn_container {
    text-align: center;
    margin: 100px 50px;

    .btn_contact {

        border: 2px solid #7fc348;
        border-radius: 20px;
        color: #7fc348;
        font-size: 20px;
        padding: 20px;
        text-decoration: none;

        &:hover {
            color: #cbeeb1;
            background-color: #3c5132;
            -webkit-box-shadow: 0px 2px 5px 2px #7fc348;
            -moz-box-shadow: 0px 2px 5px 2px #7fc348;
            box-shadow: 0px 2px 5px 2px #7fc348;

        }
    }
}